.footer {
  text-align: center;
}

.footer-menu {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.footer-menu-item {
  margin-left: 30px;
}

.footer-menu-item-link {
  color: @main-text-color;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 300;
}

.footer-menu-item-link:hover {
  color: #fff;
  transition: 0.5s;
}

.footer-menu-item:nth-child(1) {
  list-style: none;
  padding: 0;
  margin: 0;
}

.copyright {
  margin: 31px 0 70px;

  font-size: 18px;
  line-height: 1.2;
  font-weight: 300;
}

