.header {
  margin: 63px 0;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  width: 249px;
}

.search-symbol {
  transform: scale(-1, 1);
}

.search {
  margin-left: -25px;
  width: 176px;
  background-color: transparent;
  border: none;
  outline: none;
  border-bottom: 3px solid #63757e;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 300;
  color: @main-text-color;
}

.search::placeholder {
  padding-left: 25px;
  font-weight: 400;
  color: @main-text-color;
}

.search:focus,
.search:active {
  margin-left: 0px;
  transition: 0.5s;
}

@media @small-widths {
  .header-content {
    justify-content: center;
  }
  .search-block {
    display: none;
  }
}
