.content-block {
  color: #fff;
}

.top-content {
  background-image: url("../img/top-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.top-content-text {
  margin-left: 57px;
  padding: 116px 0 110px;
}

.top-content-text-title {
  font-size: 80px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
}

.top-content-text-subtitle {
  padding: 49px 0 136px;
  width: 100%;
  max-width: 350px;
  font-size: 32px;
  line-height: 1.2;
  font-weight: 300;
}

.big-content,
.small-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.big-content {
  margin: 40px 0;
}

.big-content-block,
.small-content-block {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.big-content-block {
  padding: 63px 0 223px;
  width: 100%;
  max-width: 560px;
}

.big-content-block-title {
  font-size: 48px;
  line-height: 42px;
  font-weight: 700;
  text-transform: uppercase;
}

.big-content-block-subtitle {
  padding: 28px 0 79px;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 300;
}

.big-content-block:nth-child(1) {
  padding-right: 30px;
  background-image: url(../img/content-block-bg1.jpg);
  text-align: right;
}

.big-content-block:nth-child(2) {
  padding-left: 30px;
  background-image: url(../img/content-block-bg2.jpg);
  text-align: left;
}

.small-content-block {
  width: 100%;
  max-width: 360px;
  text-align: center;
}

.small-content-block:nth-child(1) {
  background-image: url(../img/content-block-bg3.jpg);
}

.small-content-block:nth-child(2) {
  padding: 263px 0 31px;
  background-image: url(../img/content-block-bg4.jpg);
}

.small-content-block:nth-child(3) {
  background-image: url(../img/content-block-bg5.jpg);
}

.small-content-block:nth-child(odd) {
  padding: 67px 0 227px;
}

.small-content-block-descr {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 300;
}

.small-content-block-title {
  padding: 14px 0 33px;
  font-size: 36px;
  line-height: 42px;
  font-weight: 700;
  text-transform: uppercase;
}

.subscribe {
  margin: 157px 0 93px;
  text-align: center;
}

.subscribe-title {
  font-size: 36px;
  line-height: 1.2;
  font-weight: 300;
}

.subscribe-descr {
  margin: 20px 0 41px;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 300;
}

@media @medium-widths {
  .big-content,
  .small-content {
    justify-content: center;
    flex-direction: column;
  }
  .big-content-block:nth-child(1) {
    margin-bottom: 20px;
  }
  .small-content-block:nth-child(2) {
    margin: 20px 0;
  }
}

@media @small-widths {
  .top-content-text {
    margin: 0;
    padding: 30px 10px;
  }
}
