.container {
  width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}
@media screen and (max-width: 1199px) {
  .container {
    width: 970px;
  }
}
@media screen and (max-width: 991px) {
  .container {
    width: 750px;
  }
}
@media screen and (max-width: 767px) {
  .container {
    width: 100%;
    padding: 0 10px;
  }
}
body {
  background-color: #272f45;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #92a1a9;
  min-width: 320px;
  min-height: 100vh;
  font-family: "HK Grotesk", sans-serif;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
* {
  box-sizing: border-box;
}
.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background-color: #e2e2e2;
  color: #6e747c;
  font-weight: 700;
}
.select2-container--default {
  text-align: center;
  color: #6e747c;
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: #6e747c;
}
.select2-results__options {
  height: 150px;
  overflow-y: auto;
}
.select2-container--default .select2-selection--single {
  width: 345px;
  margin: 0 auto;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
  right: 5px;
}
.select2-results__options::-webkit-scrollbar {
  width: 8px;
}
.select2-results__options::-webkit-scrollbar-thumb {
  background-clip: padding-box;
  background-color: #ccc;
  border: 5px solid rgba(0, 0, 0, 0);
  border-bottom: 10px;
}
a {
  text-decoration: none;
  cursor: pointer;
}
.btn-wrapper {
  padding: 10px;
}
.btn {
  display: block;
  margin: 0 auto;
  max-width: 360px;
  padding: 21px 0;
  color: #92a1a9;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 400;
  border: 3px solid #92a1a9;
}
.btn:hover {
  padding: 25px 0;
  color: #ffffff;
  font-family: "League Spartan";
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  border: 3px solid #82c9d8;
  transition: 0.3s;
}
.buy-btn {
  padding: 20px 28px;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 400;
  color: #ffffff;
}
.buy-btn:hover {
  color: #a7a7a7;
  transition: 0.5s;
}
.fat {
  font-weight: 700;
}
@media screen and (max-width: 991px) {
  .btn {
    padding: 15px 50px;
  }
}
h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}
.title {
  font-family: "League Spartan", sans-serif;
}
.header {
  margin: 63px 0;
}
.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  width: 249px;
}
.search-symbol {
  transform: scale(-1, 1);
}
.search {
  margin-left: -25px;
  width: 176px;
  background-color: transparent;
  border: none;
  outline: none;
  border-bottom: 3px solid #63757e;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 300;
  color: #92a1a9;
}
.search::placeholder {
  padding-left: 25px;
  font-weight: 400;
  color: #92a1a9;
}
.search:focus,
.search:active {
  margin-left: 0px;
  transition: 0.5s;
}
@media screen and (max-width: 991px) {
  .header-content {
    justify-content: center;
  }
  .search-block {
    display: none;
  }
}
.footer {
  text-align: center;
}
.footer-menu {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.footer-menu-item {
  margin-left: 30px;
}
.footer-menu-item-link {
  color: #92a1a9;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 300;
}
.footer-menu-item-link:hover {
  color: #fff;
  transition: 0.5s;
}
.footer-menu-item:nth-child(1) {
  list-style: none;
  padding: 0;
  margin: 0;
}
.copyright {
  margin: 31px 0 70px;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 300;
}
.content-block {
  color: #fff;
}
.top-content {
  background-image: url("../img/top-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.top-content-text {
  margin-left: 57px;
  padding: 116px 0 110px;
}
.top-content-text-title {
  font-size: 80px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
}
.top-content-text-subtitle {
  padding: 49px 0 136px;
  width: 100%;
  max-width: 350px;
  font-size: 32px;
  line-height: 1.2;
  font-weight: 300;
}
.big-content,
.small-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.big-content {
  margin: 40px 0;
}
.big-content-block,
.small-content-block {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.big-content-block {
  padding: 63px 0 223px;
  width: 100%;
  max-width: 560px;
}
.big-content-block-title {
  font-size: 48px;
  line-height: 42px;
  font-weight: 700;
  text-transform: uppercase;
}
.big-content-block-subtitle {
  padding: 28px 0 79px;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 300;
}
.big-content-block:nth-child(1) {
  padding-right: 30px;
  background-image: url(../img/content-block-bg1.jpg);
  text-align: right;
}
.big-content-block:nth-child(2) {
  padding-left: 30px;
  background-image: url(../img/content-block-bg2.jpg);
  text-align: left;
}
.small-content-block {
  width: 100%;
  max-width: 360px;
  text-align: center;
}
.small-content-block:nth-child(1) {
  background-image: url(../img/content-block-bg3.jpg);
}
.small-content-block:nth-child(2) {
  padding: 263px 0 31px;
  background-image: url(../img/content-block-bg4.jpg);
}
.small-content-block:nth-child(3) {
  background-image: url(../img/content-block-bg5.jpg);
}
.small-content-block:nth-child(odd) {
  padding: 67px 0 227px;
}
.small-content-block-descr {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 300;
}
.small-content-block-title {
  padding: 14px 0 33px;
  font-size: 36px;
  line-height: 42px;
  font-weight: 700;
  text-transform: uppercase;
}
.subscribe {
  margin: 157px 0 93px;
  text-align: center;
}
.subscribe-title {
  font-size: 36px;
  line-height: 1.2;
  font-weight: 300;
}
.subscribe-descr {
  margin: 20px 0 41px;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 300;
}
@media screen and (max-width: 1199px) {
  .big-content,
  .small-content {
    justify-content: center;
    flex-direction: column;
  }
  .big-content-block:nth-child(1) {
    margin-bottom: 20px;
  }
  .small-content-block:nth-child(2) {
    margin: 20px 0;
  }
}
@media screen and (max-width: 991px) {
  .top-content-text {
    margin: 0;
    padding: 30px 10px;
  }
}
