a {
  text-decoration: none;
  cursor: pointer;
}
.btn-wrapper {
  padding: 10px;
}

.btn {
  display: block;
  margin: 0 auto;
  max-width: 360px;
  padding: 21px 0;
  color: @main-text-color;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 400;
  border: 3px solid @main-text-color;
}

.btn:hover {
  padding: 25px 0;
  color: #ffffff;
  font-family: "League Spartan";
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  border: 3px solid #82c9d8;
  transition: 0.3s;
}

.buy-btn {
  padding: 20px 28px;
  font-size: 24px;
  line-height: 1.2;
  font-weight: 400;
  color: #ffffff;
}

.buy-btn:hover {
  color: #a7a7a7;
  transition: 0.5s;
}

.fat {
  font-weight: 700;
}

@media @small-widths {
  .btn {
    padding: 15px 50px;
  }
}
